import React from 'react';
import { connect } from 'react-redux';

import Top from '@src/components/layout/Top';
import Bottom from '@src/components/layout/Bottom';
import Side from '@src/components/layout/Side';
import Popup from '@src/components/layout/Popup';
import Notifications from '@src/components/layout/Notifications';
import Auth from '@src/components/user/Auth';
import Nav from '@src/components/layout/Nav';

import * as layoutActions from '@src/states/layout/actions';

import '@src/components/layout/Layout.scss';


class Layout extends React.Component {
	
	render() {
		var { router } = this.props;

		return (
			<div className={"layout_container" +( this.props.layout.sidebarToggle ? " open_nav" : " " )} tabIndex={0} onKeyDown={(e)=>{ if (e.keyCode===27) { this.props.dispatch( layoutActions.popupBox({}) ); } }}>
				
				<Notifications router={router} />
				<Top router={router} />
				
				<div className="layout_content">
					
					{this.props.pages!==false &&
						<div className="pages nav_left">
							<Auth router={router}/>
							<Nav router={router} rerender={router.location.pathname}  />
						</div>
					}

					<div className="page">
						{this.props.children}
					</div>

				</div>

				{/*<Bottom router={router} />*/}
				<Side />
				<Popup />

			</div>
		);

	}
}


const mapStateToProps = (state) => {
    return {
    	user: state.user,
    	layout: state.layout
    };
};
export default connect(mapStateToProps)(Layout);


